import React from 'react';

import './MovieCard.scss';

type Card = {
  title: string;
  description: string;
  imgUrl: string;
  imdbUrl: string;
};

export const MovieCard: React.FC<Card> = ({
  imgUrl,
  title,
  description,
  imdbUrl,
}) => (
  <>
    <div className="card" data-cy="Movie">
      <div className="card-image">
        <figure className="image is-4by3" data-cy>
          <img
            data-cy="MovieImage"
            src={imgUrl}
            alt="movie"
          />
        </figure>
      </div>
      <div className="card-content">
        <div className="media">
          <div className="media-left">
            <figure className="image is-48x48">
              <img src="../images/imdb-logo.jpeg" alt="IMDB" />
            </figure>
          </div>
          <div className="media-content">
            <p
              className="title is-8"
              data-cy="MovieTitle"
            >
              {title}
            </p>
          </div>
        </div>
        <div className="content">
          <p data-cy="MovieDescription">
            {description}
          </p>
          <a
            href={imdbUrl}
            target="_blank"
            rel="noreferrer"
            data-cy="MovieLink"
          >
            IMDB
          </a>
        </div>
      </div>
    </div>
  </>
);
